@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

* {
    font-family: 'Lato', sans-serif;
}

.container {
    height: 100vh; /* Magic here */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.logo-container {
    position: absolute;
    top: 24vh /* add max pixels to prevent overlay */
}

.bar-container {
    position: absolute;
    top: 36vh /* add max pixels to prevent overlay */
}

#top-prediction-container {
    position: absolute;
    top: 48vh /* add max pixels to prevent overlay */
}

#show-more-button-container {
    position: absolute;
    top: 54vh
}

#more-predictions-container {
    position: absolute;
    top: 60vh /* add max pixels to prevent overlay */
}


input[type=text] {
    background-color: white;
    padding: 1vh 20px;
    font-size: 16px;
    text-align: left;
    border-radius: 2vh;
    border: 2px solid rgb(220, 220, 220)
}

input[type=text]:focus {
    outline: none !important;
    background-color: white;
}

input[type=text]:hover {
    box-shadow: 0 0 11px rgba(33,33,33,.2);
}

.button-container {
    text-align: center
}

button {
    background-color: rgb(220, 220, 220);
    color: rgb(57, 57, 57);
    font-size: 16px;
    padding: 1vh 24px;
    border-radius: 5px;
    border: 0px solid;
    margin: 16px 0px;
    cursor: pointer;
}

button:hover {
    border: 2px solid rgb(191, 191, 191);
    margin: 14px 0px;
}

button:active {
    border: 2px solid rgb(132, 209, 235);
    margin: 14px 0px;
}

.predictions-list {
    list-style-type: none;
    padding-top: 2px;
    padding-bottom: 2px
}

.predictions-details {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    text-align: center;
    padding-top: 4px;
    padding-bottom: 4px;
}

.predictions-details > dt {
    width: 250px;
    word-wrap: break-word;
    text-align: center;
    font-size: 22pt;
    margin: auto
}

.predictions-details > dd {
    font-size: 14pt;
    margin: auto
}